import React, { Component } from 'react';
import './App.css';

class Form extends Component {
  
 render () {
   return (
     <form className="regForm">
       <div className='input-div1'>
			<label htmlFor='firstname'>Vorname <span class="reqsymbol">*</span></label>
			<input type='firstname' id='firstname' value='' className='input1' required />
		</div>
		<div className='input-div1'>
			<label htmlFor='lastname'>Name <span class="reqsymbol">*</span></label>
			<input type='lastname' id='lastname' value='' className='input1'/>
		</div>
		<div className='input-div1'>
			<label htmlFor='email'>Email <span class="reqsymbol">*</span></label>
			<input type='email' id='email' value='' className='input1' />
		</div>
		<div className='input-div1'>
			<label htmlFor='phone'>Telefon<span class="reqsymbol">*</span></label>
			<input type='phone' id='phone' value='' className='input1'/>
		</div>
		<button type="submit" className="submitBtn">
			Anmelden
		</button>
     </form>
   )
 }
}
export default Form;